'use client';

import { usePrivy } from '@privy-io/react-auth';
import { useState } from 'react';

export default function UserProfile() {
  const { user, logout, authenticated } = usePrivy();
  const [showDropdown, setShowDropdown] = useState(false);
  const [funding, setFunding] = useState(false);

  if (!authenticated) return null;

  const displayName = user?.email?.address || 
    (user?.wallet?.address ? `${user.wallet.address.slice(0, 6)}...${user.wallet.address.slice(-4)}` : 'User');

  const handleFundWallet = async () => {
    if (!user?.wallet?.address) return;
    setFunding(true);
    try {
      const res = await fetch('/api/faucet', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ address: user.wallet.address }),
      });
      const data = await res.json();
      if (data.success) {
        alert('Funding successful! Check your wallet on Base Sepolia.');
      } else {
        alert('Funding failed: ' + (data.error || 'Unknown error'));
      }
    } catch (e) {
      alert('Funding error');
    } finally {
      setFunding(false);
    }
  };

  return (
    <div className="absolute top-4 right-4 z-50 font-sans">
      <div className="relative">
        <button
          onClick={() => setShowDropdown(!showDropdown)}
          className="flex items-center gap-3 bg-white px-4 py-2.5 rounded-full shadow-md hover:shadow-lg transition-shadow border border-gray-100"
        >
          <div className="w-8 h-8 rounded-full bg-linear-to-br from-blue-500 to-purple-600 flex items-center justify-center text-white font-bold text-xs">
            {displayName[0].toUpperCase()}
          </div>
          <span className="text-sm font-medium text-gray-700 hidden sm:block">
            {displayName}
          </span>
          <svg className={`w-4 h-4 text-gray-400 transition-transform ${showDropdown ? 'rotate-180' : ''}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
        </button>

        {showDropdown && (
          <div className="absolute right-0 mt-2 w-48 bg-white rounded-xl shadow-xl border border-gray-100 overflow-hidden py-1 animate-in fade-in slide-in-from-top-2 duration-200">
            <div className="px-4 py-3 border-b border-gray-50 bg-gray-50/50">
              <p className="text-xs text-gray-500 uppercase tracking-wider font-semibold">Signed in as</p>
              <p className="text-xs font-mono text-gray-500 truncate mt-0.5" title={user?.wallet?.address}>
                  {user?.wallet?.address ? `${user.wallet.address.slice(0, 6)}...${user.wallet.address.slice(-4)}` : 'No Wallet'}
              </p>
            </div>
            
            {/* Faucet Links */}
            <div className="p-2 border-b border-gray-50 bg-blue-50/30">
                <p className="text-[10px] font-bold text-blue-600 px-2 uppercase tracking-wider mb-1">Get Testnet Funds</p>
                <a
                    href="https://portal.cdp.coinbase.com/products/faucet"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="block px-2 py-1.5 text-xs text-gray-700 hover:bg-blue-100 rounded transition-colors flex items-center justify-between"
                >
                    <span>Base ETH ‚õΩÔ∏è</span>
                    <span className="text-gray-400">‚Üó</span>
                </a>
                 <a
                    href="https://faucet.circle.com/"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="block px-2 py-1.5 text-xs text-gray-700 hover:bg-blue-100 rounded transition-colors flex items-center justify-between"
                >
                    <span>USDC Token üí∞</span>
                    <span className="text-gray-400">‚Üó</span>
                </a>
            </div>

            <button
              onClick={logout}
              className="w-full text-left px-4 py-2.5 text-sm text-red-600 hover:bg-red-50 transition-colors flex items-center gap-2"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                 <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
              </svg>
              Sign Out
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
