'use client';

import { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase';

interface Ride {
  id: number;
  created_at: string;
  user_address: string;
  pickup_location: string;
  dropoff_location: string;
  departure_time: string;
  wechat_id: string;
  status: string;
  // Mock fields for UI demo
  price?: number;
  seats?: number;
  match_score?: number;
}

export default function RideList() {
  const [rides, setRides] = useState<Ride[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchRides();
  }, []);

  async function fetchRides() {
    try {
      const { data, error } = await supabase
        .from('rides')
        .select('*')
        .order('departure_time', { ascending: true });

      if (error) {
        throw error;
      }
      
      // Enrich with mock data for UI demonstration
      const enrichedRides = (data || []).map((ride: any) => ({
        ...ride,
        price: 10 + (ride.id % 25), // Mock price $10-$34
        seats: 1 + (ride.id % 3),   // Mock seats 1-3
        match_score: 85 + (ride.id % 15) // Mock match 85-99%
      }));
      
      setRides(enrichedRides);
    } catch (error: any) {
      console.error('Error fetching rides:', error?.message || error);
    } finally {
      setLoading(false);
    }
  }

  if (loading) return (
      <div className="flex justify-center p-8">
          <div className="w-6 h-6 border-2 border-gray-200 border-t-black rounded-full animate-spin"></div>
      </div>
  );

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between px-1">
          <h2 className="text-lg font-bold text-gray-900">Available Rides</h2>
          <span className="text-xs font-medium text-gray-500 bg-gray-100 px-2 py-1 rounded-full">{rides.length}</span>
      </div>
      
      {rides.length === 0 ? (
        <div className="text-center py-10 bg-gray-50 rounded-xl border border-dashed border-gray-200">
            <p className="text-sm text-gray-500">No rides available right now.</p>
            <p className="text-xs text-gray-400 mt-1">Be the first to create one!</p>
        </div>
      ) : (
        <div className="space-y-3">
          {rides.map((ride) => {
            const date = new Date(ride.departure_time);
            const timeStr = date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
            const dateStr = date.toLocaleDateString([], { month: 'short', day: 'numeric' });
            
            return (
                <div
                key={ride.id}
                className="group p-4 bg-white border border-gray-100 rounded-xl shadow-sm hover:shadow-md hover:border-gray-200 transition-all cursor-pointer relative overflow-hidden"
                >
                <div className="absolute left-0 top-0 bottom-0 w-1 bg-black group-hover:bg-blue-600 transition-colors"></div>
                
                <div className="flex flex-col gap-3 pl-2">
                    {/* Header: Match Score & Status */}
                    <div className="flex justify-between items-start">
                        <div className="flex items-center gap-2">
                             <div className="bg-green-100 text-green-700 px-2 py-1 rounded-md text-[10px] font-bold flex items-center gap-1">
                                <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" /></svg>
                                {ride.match_score}% Match
                            </div>
                        </div>
                        <span className={`px-2 py-1 rounded-full font-medium capitalize text-[10px] ${
                            ride.status === 'active' ? 'bg-blue-50 text-blue-700' : 'bg-gray-100 text-gray-600'
                        }`}>
                            {ride.status}
                        </span>
                    </div>

                    {/* Route */}
                    <div className="flex flex-col gap-2 relative py-1">
                        {/* Connector Line */}
                        <div className="absolute left-[5px] top-[10px] bottom-[10px] w-px bg-gray-200"></div>

                        {/* Pickup */}
                        <div className="flex items-start gap-3">
                            <div className="w-2.5 h-2.5 rounded-full border-2 border-gray-900 bg-white shrink-0 z-10 mt-1"></div>
                            <p className="text-sm font-semibold text-gray-900 leading-tight">
                                {ride.pickup_location.split(',')[0]}
                            </p>
                        </div>

                        {/* Dropoff */}
                        <div className="flex items-start gap-3">
                            <div className="w-2.5 h-2.5 rounded-sm bg-gray-900 shrink-0 z-10 mt-1"></div>
                            <p className="text-sm font-semibold text-gray-900 leading-tight">
                                {ride.dropoff_location.split(',')[0]}
                            </p>
                        </div>
                    </div>

                    {/* Info Row: Time, Price, Seats */}
                    <div className="grid grid-cols-3 gap-2 mt-2 pt-3 border-t border-gray-50">
                        <div className="flex flex-col">
                            <span className="text-[10px] text-gray-400 uppercase font-bold tracking-wider">Depart</span>
                            <span className="text-xs font-semibold text-gray-900">{timeStr}</span>
                        </div>
                        <div className="flex flex-col">
                            <span className="text-[10px] text-gray-400 uppercase font-bold tracking-wider">Per Person</span>
                            <span className="text-xs font-semibold text-gray-900">${ride.price}</span>
                        </div>
                         <div className="flex flex-col">
                            <span className="text-[10px] text-gray-400 uppercase font-bold tracking-wider">Seats</span>
                            <span className="text-xs font-semibold text-gray-900">{ride.seats} left</span>
                        </div>
                    </div>

                    {/* Footer: WeChat */}
                    <div className="flex items-center gap-2 mt-2 pt-2 border-t border-dashed border-gray-100">
                        <div className="w-4 h-4 rounded-full bg-gray-100 flex items-center justify-center">
                             <svg className="w-2.5 h-2.5 text-gray-500" fill="currentColor" viewBox="0 0 24 24"><path d="M20 2H4c-1.1 0-2 .9-2 2v16c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V4c0-1.1-.9-2-2-2zM8 8h8v2H8V8zm0 4h8v2H8v-2z" /></svg>
                        </div>
                         <p className="text-[11px] text-gray-500 truncate">
                            WeChat: <span className="font-medium text-gray-700 select-all">{ride.wechat_id}</span>
                        </p>
                    </div>
                </div>
                </div>
            );
          })}
        </div>
      )}
    </div>
  );
}
